@use "../abstracts/" as *;

.main__content {
  @include m-auto;
  margin-top: 3rem;
  padding: 0 2rem;
  
  &--flex {
    @include dflexhalf;
    gap: 7rem;

    &--left {
      flex-basis: 40%;
      border-radius: 0.7rem;

      .main__content--img{
        img {
          width: 100%;
          display: block;
          border-radius: 0.7rem;
        }
      }

      .main__content--img-gallery{
        @include dflexcomp;
        margin-top: 2rem;

        img{
          border-radius: .5rem;
          width: 20%;
          display: block;
        }
      }

    }

    &--right {
      display: flex;
      flex-direction: column;
      gap: 1rem;
      flex-basis: 40%;

      h2 {
        color: $pry-orange;
        font-weight: 900;
        font-size: 0.9rem;
      }

      h3 {
        font-size: 3rem;
      }

      p {
        color: $dark-blue;
        font-size: 1.5rem;
      }

      .main__content--pricing {
        @include dflexcomp;
        width: 45%;

        div {
          background: $sec-orange;
          color: $pry-orange;
          padding: 0.2rem 0.6rem;
          font-weight: 900;
          border-radius: 0.5rem;
          font-size: $p-size;
        }
      }
      .main__content--pricing ~ p {
        text-decoration: line-through;
      }

      .main__content--amount-flex {
        @include dflexcomp;

        .amount__container {
          @include dflexcomp;
          background: $blue;
          flex-basis: 40%;
          padding: 1rem 1rem;
          border-radius: 0.5rem;
          cursor: pointer;
        }
      }
    }
  }
}
